@import "../../_globalColor";

.certificate-card .card-image {
  max-width: 120px; /* limit maximum width */
  max-height: 120px; /* limit maximum height */
  width: auto;
  height: auto;
  object-fit: contain; /* fit logo within bounds */
}
.certificate-card .card-title {
  color: $titleColor;
  font-size: 1.375rem;
  line-height: 24px;
  font-weight: 700;
  margin: 15px 0 0 0;
}
.certificate-card .card-subtitle {
  color: $cardSubtitle;
  font-size: 1.063rem;
  line-height: 1.5rem;
}
.certificate-card {
  background-color: $lightBackground2;
  box-shadow: $lightBoxShadowDark 0px 10px 30px -15px;
  padding: 1.5rem;
  border-radius: 10px;
  border: 1px solid $lightBorder1;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.certificate-detail-div {
  text-align: center;
}
.certificate-card:hover {
  box-shadow: $lightBoxShadowDark 0px 20px 30px -10px;
}
.dark-mode.certificate-card:hover {
  box-shadow: $achievementCertificateCardHoverDark 0px 20px 30px -10px;
}
.certificate-image-div {
  position: relative;
  height: 150px; /* reduced for better proportions */
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem; /* add padding so logos don't touch edges */
}

.course-duration-badge {
  position: absolute;
  bottom: 0.5rem;
  right: 0.5rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 0.3rem 0.6rem;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.25rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(4px);
}

.duration-icon {
  font-size: 0.9rem;
  line-height: 1;
}

.duration-text {
  line-height: 1;
}
.achievement-cards-div {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem 1rem;
}
.certificate-card-footer {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
}
.certificate-card-footer span.certificate-tag {
  background: $buttonColor;
  color: $lightBackground3;
  vertical-align: middle;
  align-items: center;
  border-radius: 4px;
  display: inline-flex;
  font-size: 0.75rem;
  height: 2em;
  justify-content: center;
  white-space: nowrap;
  line-height: 1.5;
  margin: 0 0.5rem 0.5rem 0;
  padding: 0 0.75em;
  cursor: pointer;
  transition: 0.2s ease-in;
}
span.certificate-tag:hover {
  background: $buttonHover;
}

/* In Progress state for certification */
.certificate-card-footer span.certificate-tag.in-progress {
  background: linear-gradient(135deg, #ff9a56 0%, #ff6a3d 100%);
  color: $lightBackground3;
  cursor: default;
  opacity: 0.9;
  font-weight: 600;
}

.certificate-card-footer span.certificate-tag.in-progress:hover {
  background: linear-gradient(135deg, #ff9a56 0%, #ff6a3d 100%);
  transform: none;
}

.dark-mode.certificate-card .certificate-tag.in-progress {
  background: linear-gradient(135deg, #ff7f3d 0%, #ff5722 100%);
}
